.footer{
    .bg('../img/sec-9.png');
    padding: 80px 0;
    .foot-text{
        width: 100%;
        h1{
            color: #292929;
            font-size: 36px;
            font-weight: 700;
        }
    }
    .left-sidebar{
        margin-top: 60px;
        .ofis{
            p{
                font-size: 18px;
                color: #292929;
                font-weight: 400;
                line-height: 25px;
            }
            img{
                margin-top: 25px;
            }
        }
        .kont{
            margin-top: 30px;
            p{
                line-height: 25px;
                font-size: 18px;
                font-weight: 400;
                span{
                    font-size: 18px;
                    font-weight: 700;
                }
            }
        }
        .email{
            margin-top: 40px;
           p{
            font-size: 18px;
            font-weight: 400;
            color: #292929;
            line-height: 25px;
            a{
                text-decoration: underline;
                color: #292929;
            }
           }
        }
        .rek{
            margin-top: 40px;
            p{
                line-height: 25px;
                font-size: 18px;
                font-weight: 400;
                color: #292929;
                span{
                    font-size: 18px;
                    font-weight: 400;
                }
            }
        }
    }
    .right-sidebar{
        .media-block({
           margin-top: 50px;
        },991px);
        width: 100%;
        background: white;
        padding: 47px 50px;
        text-align: center;
        .forn{
            h3{
                font-size: 18px;
                font-weight: 400;
                color: #292929;
                line-height: 25px;
                margin-bottom: 20px;
            }
            form{
                input{
                    font-size: 13px;
                    font-weight: 400;
                    color: #565656;
                    width: 100%;
                    height: 54px;
                    margin-bottom: 25px;
                    &:nth-child(4){
                        height: 95px;
                    }
                }
                .btn2{
                    font-size: 14px;
                    font-weight: 400;
                    text-transform: uppercase;
                    background-image: linear-gradient(to top, #f5ad00 0%, #ffb504 100%);
                    color: #ffffff;
                    width: 100%;
                    padding: 20px 30px;
                    border: none;
                    cursor: pointer;
                    border-radius: 8px;
                }
            }
        }
    }
}
.end{
    padding: 35px 0;
    .logoo{
        .media-block({
            margin-top: 20px; 
         },991px);
        .media-block({
            display: flex;
            flex-direction: column;
            justify-content: center;
         },575px);
        display: flex;
        justify-content: flex-start;
        align-items: center;
        line-height: 25px;
        .text-logo{
            margin-left: 10px;
            h3{
                text-transform: uppercase;
                font-size: 20px;
                font-family:'Proxima Nova';
                font-weight: 600;
            }
            p{
               font-family: "Proxima Nova";
            }
        }
    }
    .tex-sec{
        .media-block({
           margin-top: 20px; 
        },991px);
        
        text-align: center;
        p{
            color: #292929;
            font-size: 17px;
            font-weight: 400;
            line-height: 24px;
            span{
                font-size: 20px;
                font-weight: 700;
                color: #292929;
            }
        }
    }
    .keng{
        .media-block({
            margin-top: 20px; 
         },991px);
         .media-block({
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
         },575px);
        text-align: end;
        p{
            font-size: 11px;
            font-weight: 300;
            text-align: end;
        }
    }
}