.section-8{
    padding: 100px 0;
    background: #009245;
    .text-8{
        text-align: center;
        margin-bottom: 50px;
        h1{
            color: #fcfcfc;
            font-size: 36px;
            font-weight: 700;
        }
        p{
            color: #fcfcfc;
            margin-top: 40px;
            font-size: 19px;
            font-weight: 400;
        }
    }
    .box-size{
        width: 100%;
        text-align: center;
        color: #fcfcfc;
        margin-bottom: 30px;
        img{
            width: 100%;
        }
        p{
            font-size: 20px;
            font-weight: 700;
            margin-top: 30px;
            color: #fcfcfc;
        }
        .btn-2{
            padding: 20px 50px;
            background-image: linear-gradient(to top, #f5ad00 0%, #ffb504 100%);
            border-radius: 8px;
            margin-top: 25px;
            text-transform: uppercase;
            font-weight: 700;
            font-size: 16px;
            cursor: pointer;
            border: none;
            color: #fcfcfc;
        }
    }
}