.section-1{
    font-family: "Proxima Nova";
    padding-top: 99px;
    padding-bottom: 100px;
    .text-1{
        text-align: center;
        margin-bottom: 130px;
        h1{
            font-size: 36px;
            font-weight: 700;
            color: #1a1820;
        }
    }
    .fakt{
        text-align: center;
        margin-bottom: 30px;
        cursor: pointer;
        &:hover{
            box-shadow: 0px 2px 10px 2px black;
        }
        img{
            margin-bottom: 64px;
        }
        h5{
            margin-bottom: 35px;
            color: #1a1820;
            font-size: 20px;
            font-weight: 700;
        }
        p{
            line-height: 20px;
            color: #1a1820;
            font-weight: 300;
            font-size: 16px;
        }
    }
}