.section-6{
    padding-top: 100px;
    .text-6{
        text-align: center;
        margin-bottom: 80px;
        h1{
            font-size: 36px;
            font-weight: 700;
            color: #000000;
        }
    }
    .sols{
        position: relative;
        .bro{
            border: 4px solid #e8e8e8;
            padding: 50px;
            width: 100%;
            h3{
                font-size: 21px;
                font-weight: 700;
            } 
            .spiska{
                li{
                    margin-top: 50px;
                    font-size: 16px;
                    font-weight: 400;
                    color: #313131;
                    cursor: pointer;
                    .fa{
                        margin-right: 20px;
                        font-size: 16px;
                        color: red;
                    }
                }
            }
        }
        .girl{
        position: absolute;
        right: 39%;
        z-index: 2;
        .media-block({
            display: none;
        },991px);
        img{
            width: 100%;
            height: 100%;
        }
        }
        .ses{
            border: 4px solid #358015;
            padding: 50px;
            width: 100%;
            .media-block({
                margin-top: 50px;
            },991px);
            h3{
                font-size: 21px;
                font-weight: 700;
            }
            .spiska{
                li{
                    margin-top: 35px;
                    font-size: 16px;
                    font-weight: 400;
                    color: #313131;
                    cursor: pointer;
                    line-height: 18px;
                    text-align: left;
                    position: relative;
                    margin-left: 20px;
                    .fa{
                        margin-right: 20px;
                        font-size: 16px;
                        color: green;
                        position: absolute;
                        left: -10%;
                    }
                }
            }
        }
    }
}