.section-3{
    font-family: "Proxima Nova";
    background: url('../img/Stroyki_27.png')no-repeat center center;
    background-size: cover;
    width: 100%;
    padding-bottom: 100px;
    padding-top: 100px;
    .text-3{
        text-align: center;
        font-size: 36px;
        font-weight: 700;
        color: #1a1820;
        margin-bottom: 100px;
    }
    .carousel{
        box-shadow: 0 12px 18px rgba(0, 0, 0, 0.11);
        padding: 50px;
        .slick-next{
            .media-block({
                width: 45px;
                height: 30px;
                transform: translateX(-10px);
            },630px);
            .media-block({
                width: 45px;
                height: 30px;
                transform: translateX(-20px);
            },570px);
            transform: translateX(23px);
            background: green;
            width: 70px;
            height: 60px;
            display: flex;
            justify-content: center;
            align-items: center;
            cursor: pointer;
            &::before{
                content: '\f105';
                font-family: 'FontAwesome';
                font-size: 30px;
                display: inline-block;
            }
        }
        .slick-prev{
            .media-block({
                width: 45px;
                height: 30px;
                transform: translateX(10px);
            },630px);
            .media-block({
                width: 45px;
                height: 30px;
                transform: translateX(20px);
            },570px);
            transform: translateX(-23px);
            background: green;
            width: 70px;
            height: 60px;
            display: flex;
            justify-content: center;
            align-items: center;
            cursor: pointer;
            &::before{
                content: '\f104';
                font-family: 'FontAwesome';
                font-size: 30px;
                display: inline-block;
            }
        }
        .rabotniki-img{
            .slick-track{
                width: 100%;
                transform: none;
            }
            .slider-for{
                margin-bottom: 20px;
                .img-2{
                    width: 510px;
                    height: 310px;
                    .media-block({
                        width: 450px;
                        height: 250px;
                    },630px);
                    .media-block({
                        width: 370px;
                        height: 200px;
                    },395px);
                    img{
                        width: 100%;
                        height: 100%;
                    }
                }
            }
            .slider-nav{
                width: 100%;
                .img-1{
                    width: 72px;
                    height: 52px;
                    margin: 10px;
                    .media-block({
                        width: 65px;
                        height: 45px;
                    },395px);
                    .media-block({
                        width: 55px;
                        height: 30px;
                        margin: 2px;
                    },336px);
                    img{
                        width: 100%;
                        height: 100%;
                    }
                }
                
            }
            .slick-track{
                width: 100%;
                transform: translate3d(0px, 0px 0px) !important;
            }
        }
        
    }
    .otsivi{
        .sena{
            p{
                font-family: "Proxima Nova";
                font-weight: 400;
                color: #1a1820;
                font-size: 16px;
                line-height: 30px;
                span{
                    font-weight: 700;
                }
            }
        }
        .zakaz{
            margin-top: 55px;
            .zakazchil{
                display: flex;
                img{
                    margin-right: 20px;
                }
                p{
                    font-size: 14px;
                    font-weight: 400;
                    line-height: 22px;
                    span{
                        font-size: 16px;
                        color: #1a1820;
                        font-weight: 700;
                    }
                }
            }
            .tex-ots{
                margin-top: 35px;
                border: 2px solid #49bf15;
                padding: 20px;
                position: relative;
                &::before{
                    content: '';
                    display: block;
                    position: absolute;
                    top: -1.9rem;
                    left: 2.5rem;
                    width: 2rem;
                    height: 2rem;
                    background-color: #ffffff;
                    border: 2px solid #70c849;
                    border-bottom: none;
                    border-top-right-radius: 100px;
                   }
                   p{
                    font-size: 15px;
                    font-weight: 400;
                    line-height: 18px;
                    color: #1a1820;
                }
            }
        }
    }
  
    .vopros{
        font-family: "Proxima Nova";
        .tex-vop{
            margin-top: 100px;
            text-align: center;
            margin-bottom: 40px;
            h1{
                font-size: 22px;
                font-weight: 700;
                color: #1a1820;
            }
        }
        .foromu{
            display: flex;
            justify-content: center;
            .input-1{
                width: 100%;
                height: 54px;
                border-radius: 5px;
                border: 1px solid #dddddd;
            }
            .input-2{
                width: 100%;
                height: 54px;
                border-radius: 5px;
                border: 1px solid #dddddd;
                .media-block({
                    margin-top: 20px;
                },991px);
            }
            .btn-oronge{
                margin-top: 30px;
                width: 100%;
                height: 65px;
                border: 1px solid #ffba15;
                box-shadow: 0 8px 13px rgba(0, 0, 0, 0.04);
                background-image: linear-gradient(to top, #f5ad00 0%, #ffb504 100%);
                cursor: pointer;
                outline: none;
                font-size: 16px;
                font-weight: 400;
                text-transform: uppercase;
                color: #ffffff;
                display: flex;
                justify-content: center;
                align-items: center;
            }
        }
    }
}