.header-1{
    background: url('../img/bg.png')no-repeat center center;
    background-size: cover;
    max-width: 100%;
    padding-top: 30px;
    .menu-gamb{
        .menu-lio.active{
            transform: translateX(0%);
        }
        .menu-lio{
            padding-top: 50px;
            color: white;
            transform: translateX(-100%);
            flex-direction: column;
            background: linear-gradient(#D4145A , #FBB03B);
            width: 100%;
            position: fixed;
            height: 100%;
            z-index: 2;
            top: 0;
            transition: 1s;
            .ikon{
                position: absolute;
                top: 5%;
                right: 5%;
                cursor: pointer;
                .fa{
                    font-size: 45px;
                }
            }
            .logoo{
                display: flex;
                justify-content: center;
                align-items: center;
                line-height: 25px;
                .text-logo{
                    margin-left: 10px;
                    h3{
                        text-transform: uppercase;
                        font-size: 20px;
                        font-family:'Proxima Nova';
                        font-weight: 600;
                    }
                    p{
                       font-family: "Proxima Nova";
                    }
                }
            }
            .menu-find{
                display: flex;
                flex-direction: column;
                align-items: center;
                margin-top: 100px;
                li{
                    margin: 15px 0;
                    a{
                        text-decoration: none;
                        font-size: 22px;
                        font-weight: 600;
                        color: white;
                        cursor: pointer;
                        &:hover{
                            color: rgb(0, 71, 118);
                        }
                    }
                }
            }
        }
        .menu-go{
            display: none;
            position: fixed;
            top: 10%;
            right: 10%;
            z-index: 1;
            .md-block({
               display: flex; 
            });
            .fa{
                font-size: 35px;
                color: red;
                cursor: pointer;
            }
        }
    }
    .logo{
        display: flex;
        justify-content: center;
        align-items: center;
        line-height: 25px;
        .md-block({
            display: none; 
         });
        .text-logo{
            margin-left: 10px;
            h3{
                text-transform: uppercase;
                font-size: 20px;
                color: #242424;
                font-family:'Proxima Nova';
                font-weight: 600;
            }
            p{
               color: #5e5e5d;
               font-family: "Proxima Nova";
            }
        }
    }
    .contact{
        display: flex;
        align-items: center;
        .md-block({
            display: none; 
         });
        p{
            color: #2a2a2a;
            font-size: 20px;
            font-weight: 700;
            font-family: "Proxima Nova";
            .media-block({
                font-size: 13px;
            },1199px);
        }
        .btn{
            background: #feb404;
            border: none;
            outline: none;
            cursor: pointer;
            border-radius: 8px;
            padding: 15px;
            font-family: "Proxima Nova";
            font-size: 12px;
            color: #ffffff;
            margin-left: 30px;
            .media-block({
                margin-left: 20px;
            },1199px);
        }
    }
    .centr{
        margin-top: 104px;
        font-family: "Proxima Nova";
        .media-block({
            text-align: center;
        },767px);
        h1{
            line-height: 58px;
            font-weight: 700;
            font-size: 40px;
            text-transform: uppercase;
            color: #292929;
            margin-bottom: 51px;
            .media-block({
                font-size: 35px;
            },1199px);
            .media-block({
                font-size: 26px;
                line-height: 45px;
            },991px);
            .media-block({
                font-size: 20px;
                line-height: 25px;
            },353px);
        }
        p{
            font-size: 24px;
            font-weight: 400;
            color: #292929;
            line-height: 28px;
            margin-bottom: 60px;
            .media-block({
                font-size: 18px;
            
            },353px);
        }
        span{
            display: flex;
            justify-content: center;
            align-items: center;
            text-align: center;
            width: 225px;
            height: 105px;
            text-decoration: none;
            font-size: 18px;
            font-weight: 400;
            color: #ffffff;
            background:#70c849;
            line-height: 25px;
            cursor: pointer;
            margin-bottom: 37px;
            .media-block({
                margin: auto;
                margin-bottom: 30px;
            },767px);
        }
        h4{
            color: #292929;
            font-size: 22px;
            text-transform: uppercase;
            font-weight: 700;
            margin-bottom: 107px;
            .media-block({
                margin-bottom: 25px;
            },767px);
        }
    }
    .forma{
        width: 355px;
        height: 410px;
        background: white;
        padding-top: 46px;
        box-sizing: border-box;
        font-family: "Proxima Nova";
        margin-top: 104px;
        .media-block({
            margin-top: 50px;
            margin: auto;
            margin-bottom: 50px;
        },767px);
        .media-block({
            width: 280px;
            height: 410px;
        },412px);
        p{
            color: #292929;
            font-family: "Proxima Nova";
            font-size: 18px;
            text-align: center;
            line-height: 23px;
            font-weight: 400;
            margin-bottom: 39px;
        }
        form{
            display: flex;
            justify-content: center;
            flex-direction: column;
            align-items: center;
            .input-1{
                width: 256px;
                height: 54px;
                border-radius: 5px;
                border: 1px solid #dddddd;
                font-size: 13px;
                color: #a4a4a4;
                font-weight: 400;
                text-align: center;
                margin-bottom: 20px;
                .media-block({
                    width: 180px;
                },412px);
            }
            .input-2{
                width: 256px;
                height: 54px;
                border-radius: 5px;
                border: 1px solid #dddddd;
                font-size: 13px;
                color: #a4a4a4;
                font-weight: 400;
                text-align: center;
                .media-block({
                    width: 180px;
                },412px);
            }
            button{
                margin-top: 10px;
                font-size: 15px;
                color: white;
                text-transform: uppercase;
                font-weight: 400;
                padding: 27px 55px;
                background: #fcb303;
                border: none;
                cursor: pointer;
                outline: none;
                .media-block({
                    padding: 15px 20px;
                },412px);
                &:hover{
                    box-shadow: 3px 5px 10px 5px rgb(106, 105, 105);
                }
            }
        }
        h5{
            font-size: 12px;
            color: #565656;
            font-weight: 400;
            text-align: center;
            margin-top: 15px;
        }
    }
}