.section-4{
    .bg('../img/section-4_02.png');
    width: 100%;
    font-family: "Proxima Nova";
    padding-bottom: 100px;
    .text-4{
        margin-top: 100px;
        text-align: center;
        h1{
            font-size: 36px;
            font-weight: 700;
            color: #1a1820;
        }
        p{
            font-size: 16px;
            font-weight: 400;
            color: #1a1820;
            margin-top: 40px;
        }
    }
    .dol{
        margin-top: 60px;
        box-sizing: border-box;
        .sxema{
            .media-block({
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;
           },767px);
            color: #1a1820;
            h3{
                font-size: 22px;
                font-weight: 700;
                margin-top: 30px;
            }
            p{
                font-size: 17px;
                margin-top: 25px;
                font-weight: 400;
                line-height: 18px;
            }
        }
    }
}