.section-2{
    padding-top: 120px;
    padding-bottom: 50px;
    background: linear-gradient(#00537E , #3AA17E);
    font-family: "Proxima Nova";
    .text-2{
        display: flex;
        justify-content: center;
        position: relative;
        margin-bottom: 90px;
        .sena{
            text-align: center;
            h1{
                color: #ffffff;
                font-size: 36px;
                font-weight: 700;
                margin-bottom: 49px;
            }
            p{
                font-size: 18px;
                font-weight: 400;
                color: #ffffff;
            }
        }
        .rasrochka{
            position: absolute;
            top: 0;
            right: 0;
            .media-block({
                 top: 10%;   
            },767px);
            .media-block({
                 top: 40%;   
                 left: 20%;
            },497px);
            .btn-2{
                font-size: 18px;
                text-transform: uppercase;
                color: #ffffff;
                font-weight: 400;
                cursor: pointer;
                border: none;
                outline: none;
                background: #feb504;
                padding: 26px 33px;
                .media-block({
                    font-size: 12px;
                    padding: 15px 20px;
                },991px);
                .media-block({
                    font-size: 9px;
                    padding: 10px 15px;
                },767px);
                &:hover{
                    box-shadow: 2px 10px 18px rgb(255, 255, 255);
                }
            }
        }
    }
    .prace{
        background: white;
        padding: 50px 50px 45px 50px;
        text-align: center;
        margin-bottom: 50px;
        &:hover{
            box-shadow: 0 8px 13px rgb(255, 255, 255);
        }
        h5{
            font-size: 19px;
            font-weight: 700;
            margin-bottom: 34px;
            color: #1a1820;
        }
        img{
            margin-bottom: 34px;
        }
        h6{
            font-size: 30px;
            color: #1a1820;
            font-weight: 700;
            margin-bottom: 34px;
            .media-block({
                font-size: 25px;
            },329px);
        }
        p{
            line-height: 30px;
            font-size: 18px;
            font-weight: 400;
            color: #1a1820;
            margin-bottom: 112px;
            position: relative;
            &::before{
                content: '\f058';
                font-family: 'FontAwesome';
                position: absolute;
                top: 0;
                left: 10%;
                .media-block({
                    left: 0%;
                },1199px);
                .media-block({
                    left: 25%;
                },767px);
                .media-block({
                    left: 20%;
                },513px);
                .media-block({
                    left: 0%;
                },456px);
            }
            &::after{
                content: '\f058';
                font-family: 'FontAwesome';
                position: absolute;
                top: 50%;
                left: 28%;
                .media-block({
                    left: 25%;
                },1199px);
                .media-block({
                    left: 35%;
                },767px);
                .media-block({
                    left: 20%;
                },456px);
            }
        }
        .zakaz{
            font-size: 18px;
            font-weight: 400;
            color: #ffffff;
            text-transform: uppercase;
            padding: 26px 60px;
            background: #feb403;
            border-radius: 8px;
            border: none;
            outline: none;
            cursor: pointer;
            &:hover{
                box-shadow: 0 8px 13px rgb(0, 0, 0);
            }
        }
    }
   
}