.section-5{
    font-family: "Proxima Nova";
    background-color: #f5f5f5;
    padding-top: 100px;
    padding-bottom: 100px;
    .text-5{
        text-align: center;
        h1{
            font-size: 48px;
            color: #358015;
            font-weight: 700;
            .md-block({
               font-size: 40px; 
            });
            .sm-block({
               font-size: 35px; 
            });
        }
        p{
            font-size: 16px;
            font-weight: 400;
            color: #1a1820;
            margin-top: 40px;
        }
    }
    .nemo{
        margin-top: 100px;
        .stv{
            width: 100%;
            display: flex;
            text-align: left; 
            align-items: center;
        
            img{
                margin-right: 10px;
            }
            p{
                font-size: 13px;
                font-weight: 300;
                line-height: 18px; 
            span{
                line-height: 18px;
                font-size: 16px;
                font-weight: 700;
                color: #000000;
            }
        }
        }
    }
}